@import './common';
@import './modal.scss';

.instruments-card {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;

  margin: 50px auto;
  width: 1200px;
  padding: 30px;
}

.instruments-card li {
  flex-basis: calc((100% - 60px) / 3);

  padding: 20px;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  border-radius: 10px;
}

.instruments-card img {
  width: 100%;
  margin: 0 auto;
}

.instruments-card {
  text-align: center;
}
.instruments-card a {
  display: block;
  margin-bottom: 10px;
}

.btn {
  align-items: center;
  appearance: none;
  background-clip: padding-box;
  background-color: initial;
  background-image: none;
  border-style: none;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  flex-direction: row;
  flex-shrink: 0;
  font-family: Eina01, sans-serif;
  font-size: 16px;
  font-weight: 800;
  justify-content: center;
  line-height: 24px;
  margin: 0;
  min-height: 64px;
  outline: none;
  overflow: visible;
  padding: 19px 26px;
  pointer-events: auto;
  position: relative;
  text-align: center;
  text-decoration: none;
  text-transform: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: middle;
  width: auto;
  word-break: keep-all;
  z-index: 0;
  margin-bottom: 10px;
}

@media (min-width: 768px) {
  .btn {
    padding: 19px 32px;
  }
}

.btn:before,
.btn:after {
  border-radius: 80px;
}

.btn:before {
  background-image: linear-gradient(92.83deg, #ff7426 0, #f93a13 100%);
  content: '';
  display: block;
  height: 100%;
  left: 0;
  overflow: hidden;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: -2;
}

.btn:after {
  background-color: initial;
  background-image: linear-gradient(#541a0f 0, #0c0d0d 100%);
  bottom: 4px;
  content: '';
  display: block;
  left: 4px;
  overflow: hidden;
  position: absolute;
  right: 4px;
  top: 4px;
  transition: all 100ms ease-out;
  z-index: -1;
}

.btn:hover:not(:disabled):before {
  background: linear-gradient(
    92.83deg,
    rgb(255, 116, 38) 0%,
    rgb(249, 58, 19) 100%
  );
}

.btn:hover:not(:disabled):after {
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  transition-timing-function: ease-in;
  opacity: 0;
}

.btn:active:not(:disabled) {
  color: #ccc;
}

.btn:active:not(:disabled):before {
  background-image: linear-gradient(
      0deg,
      rgba(0, 0, 0, 0.2),
      rgba(0, 0, 0, 0.2)
    ),
    linear-gradient(92.83deg, #ff7426 0, #f93a13 100%);
}

.btn:active:not(:disabled):after {
  background-image: linear-gradient(#541a0f 0, #0c0d0d 100%);
  bottom: 4px;
  left: 4px;
  right: 4px;
  top: 4px;
}

.btn:disabled {
  cursor: default;
  opacity: 0.24;
}

.filter-input {
  padding: 10px;
  font-size: 20px;
  width: 500px;
  height: 50px;
  display: block;
  margin: 0 auto;
  box-shadow: rgba(0, 0, 0, 0.1) 1px 1px 3px 0px,
    rgba(0, 0, 0, 0.06) 0px 1px 2px 0px;
  border: none;
  border-radius: 10px;
  background-color: #f1eeee;
  outline: none;
}

.filter-input:focus {
  border: 3px solid rgb(255, 116, 38);
}
